@import url("http://fonts.googleapis.com/css2?family=Sora&display=swap");

* {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  padding: 0;
}

/* Main body */

body {
  margin: 0px;
  background-color: black;
}

.HeaderbgTextGradient {
  position: absolute;
  width: 60%;
  top: 0px;
  height: 100%;
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(0, 0, 0, 0.6)),
    to(rgba(0, 0, 0, 0))
  );
  background-image: -webkit-linear-gradient(
    left,
    rgba(0, 0, 0, 0.6),
    rgba(0, 0, 0, 0)
  );
  background-image: -o-linear-gradient(
    left,
    rgba(0, 0, 0, 0.6),
    rgba(0, 0, 0, 0)
  );
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.6),
    rgba(0, 0, 0, 0)
  );
}
.HeaderbgTextGradientMob {
  background-image: -webkit-gradient(
    linear,
    left top, right top,
    from(rgba(0, 0, 0, 0.85)),
    to(rgba(0, 0, 0, 0))
  );
  background-image: -webkit-linear-gradient(
    left,
    rgba(0, 0, 0, 0.85),
    rgba(0, 0, 0, 0)
  );
  background-image: -o-linear-gradient(
    left,
    rgba(0, 0, 0, 0.85),
    rgba(0, 0, 0, 0)
  );
  background-image: linear-gradient(
    to right,
    rgba(0, 0, 0, 0.85),
    rgba(0, 0, 0, 0)
  );
  width: 235px;
  height: 684px;
  margin: 0 17px 0 0;
  padding: 492px 33px 155px 15px;
}
.MuiInputBase-root.MuiInputBase-root {
  color: #ecbd46;
}

p {
  color: #ffffff;
}

.bodyComponentLayout {
  margin-inline: 75px;
}
/* Navigation css*/
.MuiAppBar-positionStatic.MuiAppBar-positionStatic {
  background-color: transparent;
}
.MuiList-root {
  padding-top: 0px !important;
  padding-bottom: 0px !important;
}
/* background image sytles */
.img-container {
  position: relative;
}
.imagShadow {
  -webkit-box-shadow: 3px 10px 10px 0 rgba(0, 0, 0, 0.75) !important;
          box-shadow: 3px 10px 10px 0 rgba(0, 0, 0, 0.75) !important;
}

.centered {
  position: absolute;
  top: 42%;
  left: 5%;
}

.MuiPaper-root.MuiPaper-root.MuiPaper-root.MuiPaper-root {
  background-color: transparent;
}
.centered_2 {
  position: absolute;
  top: 35%;
  -webkit-padding-start: 50vw;
          padding-inline-start: 50vw;
  min-width: 90vw;
  -webkit-transform: translate(-50%, -50%);
      -ms-transform: translate(-50%, -50%);
          transform: translate(-50%, -50%);
  font-size: 2.3rem;
}
.MuiTypography-colorTextSecondary.MuiTypography-colorTextSecondary {
  color: #ffffff;
}
.MuiPaper-root.MuiPaper-root {
  background-color: transparent;
}
.MuiContainer-root.MuiContainer-root {
  padding-left: 0px !important;
  padding-right: 0px !important;
}

.IpadPro,
.mobile,
.IpadQuer {
  display: none;
}
.dash {
  width: 14%;
  margin-top: -40px;
}
.bottom-yellow-line-margin {
  margin-top: -15px;
}
.image {
  max-height: 350px;
}
.responsive {
  width: 34%;
  /* max-width: 167px; */
  height: auto;
}

/* media querry*/
.textHeaderColor {
